// Main DashBoard "draver" Page

import React from "react";
import { useEffect, useState } from "react";
import "../Dashboard/Dashboard.css";
import { styled, useTheme } from "@mui/material/styles";
import Box from "@mui/material/Box";
import MuiDrawer from "@mui/material/Drawer";
import MuiAppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import List from "@mui/material/List";
import CssBaseline from "@mui/material/CssBaseline";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import { product, dashboard } from "../Dashboard/Product";
import { Body } from "./Body";
import { Avatar, Button, Stack } from "@mui/material";
import Logoimg from "../Images/Logooo.png";
import Logout from "../Images/Icons/Logout.png";
import {
  BrowserRouter,
  Link,
  Route,
  Routes,
  useNavigate,
} from "react-router-dom";
import UserManagment from "../Dashboard/user managment/UserManagment.js";
import CustomerManagmentResult from "../Dashboard/user managment/CustomerManagmentResult.js";
import QuestionManagmentLanguage from "../Dashboard/Question Managment Components/QuestionManagmentLanguage.js";
import QuestionManagmentEnglish from "../Dashboard/Question Managment Components/QuestionManagmentEnglish.js";
import QuestionManagmentHindi from "../Dashboard/Question Managment Components/QuestionManagmentHindi.js";
import AddQuestion from "../Dashboard/Question Managment Components/AddQuestion.js";
import GeneralScience from "../Dashboard/Question Managment Components/English Question/GeneralScience.js";
import Reasoning from "../Dashboard/Question Managment Components/English Question/Reasoning.js";
import QuantativeAptitude from "../Dashboard/Question Managment Components/English Question/QuantativeAptitude.js";
import ComputerScience from "../Dashboard/Question Managment Components/English Question/ComputerScience.js";
import Sports from "../Dashboard/Question Managment Components/English Question/Sports.js";
import Entertainment from "../Dashboard/Question Managment Components/English Question/Entertainment.js";
import GeneralScienceसामान्यविज्ञान from "../Dashboard/Question Managment Components/Hindi Question/GeneralScienceसामान्यविज्ञान.js";
import Reasoningतर्क from "../Dashboard/Question Managment Components/Hindi Question/Reasoningतर्क.js";
import QuantitativeAptitudeमात्रात्मकयोग्यता from "../Dashboard/Question Managment Components/Hindi Question/QuantitativeAptitudeमात्रात्मकयोग्यता.js";
import ComputerScienceकंप्यूटरविज्ञान from "../Dashboard/Question Managment Components/Hindi Question/ComputerScienceकंप्यूटरविज्ञान.js";
import Sportsखेलकूद from "../Dashboard/Question Managment Components/Hindi Question/Sportsखेलकूद.js";
import Entertainmentमनोरंजन from "../Dashboard/Question Managment Components/Hindi Question/Entertainmentमनोरंजन.js";
import LogoutRoundedIcon from '@mui/icons-material/LogoutRounded';

import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Modal from "react-bootstrap/Modal";

import CloseIcon from "@mui/icons-material/Close";

console.log(product, "product");
const drawerWidth = 240;
const openedMixin = (theme) => ({
  width: drawerWidth,
  transition: theme.transitions.create("width", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen,
  }),
  overflowX: "hidden",
});

const closedMixin = (theme) => ({
  transition: theme.transitions.create("width", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  overflowX: "hidden",
  width: `calc(${theme.spacing(7)} + 1px)`,
  [theme.breakpoints.up("sm")]: {
    width: `calc(${theme.spacing(8)} + 1px)`,
  },
});

const DrawerHeader = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "flex-end",
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
}));

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  transition: theme.transitions.create(["width", "margin"], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  width: drawerWidth,
  flexShrink: 0,
  whiteSpace: "nowrap",
  boxSizing: "border-box",
  ...(open && {
    ...openedMixin(theme),
    "& .MuiDrawer-paper": openedMixin(theme),
  }),
  ...(!open && {
    ...closedMixin(theme),
    "& .MuiDrawer-paper": closedMixin(theme),
  }),
}));

function Dashboard() {
  const navigate = useNavigate();
  useEffect(() => {
    handleDrawerOpen();
  }, []);

  const theme = useTheme();
  const [open, setOpen] = React.useState(false);

  const [deleteModal, setDeleteModal] = useState({
    open: false,
  });

  const handleDrawerOpen = () => {
    setOpen(true);
    document.getElementById("Admin").style.display = "block";
    document.getElementById("logout").style.display = "none";
    document.getElementById("Admin1").style.display = "none";
    document.getElementById("logout1").style.display = "block";
  };

  const handleDrawerClose = () => {
    setOpen(false);
    document.getElementById("Admin").style.display = "none";
    document.getElementById("logout").style.display = "block";
    document.getElementById("Admin1").style.display = "block";
    document.getElementById("logout1").style.display = "none";
  };

  useEffect(() => {
    if (!localStorage.getItem("token")) {
      navigate("/admin");
    }
  }, []);

  const logout = () => {
    localStorage.clear();
    navigate("/admin");
    toast.error("Admin Logout", {
      position: toast.POSITION.TOP_RIGHT,
      autoClose: 1000,
    });
    setTimeout(1000);
    deleteModal.open = false;
  };

  return (
    <>
      <div>
        <Modal
          show={deleteModal.open}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header>
            <Modal.Title id="contained-modal-title-vcenter">
              Logout Admin
            </Modal.Title>
            <CloseIcon
              style={{ cursor: "pointer" }}
              onClick={() => setDeleteModal({ open: false })}
            />
          </Modal.Header>
          <Modal.Body>Are you sure you want to logout ?</Modal.Body>
          <Modal.Footer>
          <Button
              variant="primary"
              style={{ background: "red" }}
              onClick={() => logout(deleteModal)}
            >
              Yes
            </Button>
            <Button
              variant="primary"
              onClick={() => setDeleteModal({ open: false })}
            >
              No
            </Button>
           
          </Modal.Footer>
        </Modal>
      </div>
      <Box  sx={{ display: "flex" }}>
        <CssBaseline />
        <AppBar position="fixed" open={open} className="header">
          <Toolbar className="navbar">
            <IconButton
              color="inherit"
              className="icon"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              edge="start"
              sx={{
                marginRight: 5,
                ...(open && { display: "none" }),
              }}
            >
              <MenuIcon sx={{ color: "white" }} />
            </IconButton>
          </Toolbar>
        </AppBar>
        <Drawer variant="permanent" open={open} className="drawer">
          <List className="drawerMain sc">
            <DrawerHeader>
              <IconButton onClick={handleDrawerClose}>
                {theme.direction === "rtl" ? (
                  <ChevronRightIcon />
                ) : (
                  <ChevronLeftIcon className="lefticon" />
                )}
              </IconButton>
            </DrawerHeader>
            <Box id="Admin">
              <Stack direction="row" spacing={2}>
                <Avatar
                  alt="Logo"
                  src={Logoimg}
                  sx={{ margin: "auto", height: "auto", width: "80px" }}
                />
              </Stack>
            </Box>
            {dashboard.map((text) => {
              return (
                <ListItem
                  key={text}
                  disablePadding
                  sx={{ display: "block" }}
                  className="drawerList"
                  onClick={() => navigate(text.path)}
                >
                  <ListItemButton
                    sx={{
                      minHeight: 40,
                      justifyContent: open ? "initial" : "center",
                      px: 2.5,
                    }}
                  >
                    <ListItemIcon
                      sx={{
                        color: "white",
                        minWidth: 0,
                        mr: open ? 3 : "auto",
                        justifyContent: "center",
                      }}
                    >
                      {text.icon}
                    </ListItemIcon>

                    {/* Routing */}
                    <ListItemText
                      primary={text.name}
                      sx={{ opacity: open ? 1 : 0 }}
                    />
                  </ListItemButton>
                </ListItem>
              );
            })}

            <Box>
              <ListItem className="avtarlist">
                <Box sx={{ mt: "-40px" }}>
                  <Stack direction="row" spacing={2} id="Admin1">
                    <Avatar
                      className="SetAvtarPosition"
                      alt="Logo"
                      src={Logoimg}
                    />
                  </Stack>
                  <Button
                    className="Logout"
                    onClick={() => setDeleteModal({ open: true })}
                    id="logout"
                  >
                    
                  Logout
                    <LogoutRoundedIcon className="Logbtn" />
                  </Button>
                  <Button
                    className="Logout1"
                    onClick={() => setDeleteModal({ open: true })}
                    id="logout1"
                  >
                  Logout
                    <LogoutRoundedIcon className="Logbtn" />
                    
                              {/* <img src={Logout} className="Logbtn" /> */}
                  </Button>
                </Box>
              </ListItem>
            </Box>
          </List>
        </Drawer>

        <Box component="main" sx={{ flexGrow: 1 }}>
          <DrawerHeader />

          <Routes>
            <Route path="/" element={<Body />} />
            {/* Customer Managment Table Page Routing */}
            <Route path="/UserManagment" element={<UserManagment />} />
            {/* Customer Managment "Edit" Table Page Routing */}
            <Route
              path="customerManagmentResult"
              element={<CustomerManagmentResult />}
            />
            {/* Question Managment Page Routing*/}
            <Route
              path="/QuestionManagmentLanguage"
              element={<QuestionManagmentLanguage />}
            />
            <Route
              path="/QuestionManagmentEnglish"
              element={<QuestionManagmentEnglish />}
            />
            <Route
              path="/QuestionManagmentHindi"
              element={<QuestionManagmentHindi />}
            />
            <Route path="/AddQuestion" element={<AddQuestion />} />
            {/* English All Question Page Routing */}
            <Route path="/GeneralScience" element={<GeneralScience />} />
            <Route path="/Reasoning" element={<Reasoning />} />
            <Route
              path="/QuantativeAptitude"
              element={<QuantativeAptitude />}
            />
            <Route path="/ComputerScience" element={<ComputerScience />} />
            <Route path="/Sports" element={<Sports />} />
            <Route path="/Entertainment" element={<Entertainment />} />
            {/* Hindi All Question Page Routing */}
            <Route
              path="/GeneralScienceसामान्यविज्ञान"
              element={<GeneralScienceसामान्यविज्ञान />}
            />
            <Route path="/Reasoningतर्क" element={<Reasoningतर्क />} />
            <Route
              path="/QuantitativeAptitudeमात्रात्मकयोग्यता"
              element={<QuantitativeAptitudeमात्रात्मकयोग्यता />}
            />
            <Route
              path="/ComputerScienceकंप्यूटरविज्ञान"
              element={<ComputerScienceकंप्यूटरविज्ञान />}
            />
            <Route path="/Sportsखेलकूद" element={<Sportsखेलकूद />} />
            <Route
              path="/Entertainmentमनोरंजन"
              element={<Entertainmentमनोरंजन />}
            />
          </Routes>
        </Box>
      </Box>
    </>
  );
}

export default Dashboard;
